body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.background {
  background-image: url(/static/media/background.73adc9ff.png);
  background-repeat: no-repeat;
  background-size: cover;

}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

p {
    color: #1b1b1b;
}

.wrap {
    margin: 30px 0;
    border: 1px solid #1b1b1b;
    padding: 20px;
    border-radius: 10px;
    width: 50%;
}

.link {
    text-decoration: none;
}

.date {
    margin-left: 30px;
    font-size: 12px;
}

.location {
    font-size: 24px;
}

.user-pic {
    height: 40px;
    margin-right: 10px;
}

.user-wrap {
    display: -webkit-flex;
    display: flex;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    text-decoration: none;
    color: #1b1b1b;

}

.user-name {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.flex {
    display: -webkit-flex;
    display: flex;
}

.v-center {
    -webkit-align-items: center;
            align-items: center;
}

.h-center {
    -webkit-justify-content: center;
            justify-content: center;
}
.Login {
    height: 100%;
    width: 100%;
    position: fixed;
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    background-image: url(/static/media/background.73adc9ff.png);
    background-repeat: no-repeat;
    background-size: cover;
}

.register {
    margin-left: 10px;
}
.trip-wrap {
    padding: 30px;
    position: fixed;
    height: 100%;
    width: 100%;
    background-image: url(/static/media/background.73adc9ff.png);
    background-repeat: no-repeat;
