}
.user-view-wrap {
    padding: 20px 50px;
    background-color: #62615f;
}

.user-topbar-wrap {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}

.user-name-wrap {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.user-name {
    margin-left: 20px;
    font-size: 24px;
}

.large-user-pic {
    width: 100px;
}

.message-button {
