input {
    border: none;
    border-bottom: 1px solid rgb(197, 199, 201);
    margin-bottom: 15px;
}

label, input, button {
    font-size: 18px;
}

button {
    border: 1px solid rgb(197, 199, 201);
    background: none;
    padding: 5px 10px;
    border-radius: 4px;
}
.topbar-wrap {
    padding: 10px 30px;
    border-bottom: 1px solid rgb(197, 199, 201);
    background-color: #fff;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.log-out {
